@import url(https://fonts.googleapis.com/css2?family=Crimson+Pro:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}

* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  font-size: 16px;
  font-family: 'Crimson Pro', serif;
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

.mobileCenter {
  display: flex;
  justify-content: center;
}

.condiv {
  padding: 100px;
  background-color: #f4f5f9;
  flex: 6 1;
  flex: var(--condiv-flex) 1;
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

a {
  text-decoration: none;
  color: #000;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
}

p {
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}

h1, h2, h3, h4, p {
  margin: 10px;
}

.card {
  padding: 30px;
  margin-bottom: 20px;
}

.card:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.active {
  font-weight: bolder;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }

  .App {
    flex-direction: column;
  }

  .sidebar {
    padding: 30px;
  }

  .mobileCenter img {
    max-width: 150px;
  }

  .condiv {
    flex: 0 1;
    padding: 15px;
  }
}

